<script>
import axios from "axios";

export default {
  name: "Employee",
  props: {
    msg: String,
  },
  data() {
    return {
      form: {
        firstname: "",
        lastname: "",
        username: "",
        password: "",
        idnumber: "",
        repassword: "",
        educations: false,
        bills: false,
        advertise: false,
        product: false,
        requests: false,
        tickets: false,
        orders: false,
        identifying: false,
        allies: false,
        settings: false,
      },
      emps: [],
    };
  },
  methods: {
    submit() {
      let url = process.env.VUE_APP_API_URL + "employees";
      console.log(this.form);
      if (this.form.password == this.form.repassword) {
        axios.post(url, this.form).then(function (res) {
          console.log(res);
          if (res.status == 200) {
            window.location.reload();
            alert("Done!!!");
          }
        });
      } else {
        alert(" ");
      }
    },
    async deleteit(id) {
      let url = process.env.VUE_APP_API_URL + "employees/" + id;
      await axios.delete(url).then((response) => {
        console.log(response);
        if (response.status == 200) {
          window.location.reload();
        } else {
          alert("   ");
        }
      });
    },
  },
  created() {
    let url = process.env.VUE_APP_API_URL + "employees";
    axios.get(url).then((res) => {
      this.emps = res.data;
      console.log(this.emps);
    });
  },
};
</script>

